import "./intro.css";
import myPic from "../../img/myPic.jpg";

const Intro = () => {
    return (
    <div className="intro">
        <div className="intro-left">
            <div className="intro-left-wrapper">
                <h1 className="intro-name">ASHLEY ALBERTSON</h1>
                <h2 className="intro-role">Full Stack Developer</h2>
                <div className="intro-skills">
                    <div className="intro-skills-wrapper">
                        <div className="intro-skills-item">CSS</div>
                        <div className="intro-skills-item">JavaScript</div>
                        <div className="intro-skills-item">React.js</div>
                        <div className="intro-skills-item">MySQL</div>
                        <div className="intro-skills-item">Node.js</div>
                    </div>

                </div>
            </div>
        </div>
        <div className="intro-right">
            <div className="image-bg"></div>
            <img src= {myPic} alt="Ashley Albertson Full Stack Developer" className="my-pic" />

        </div>

    </div>
    )
};

export default Intro;